$(document).ready(function () {
    $("#contact-form").validate({
        rules: {
            "name": {
                required: true,
                minlength: 5
            },
            "email": {
                required: true,
                email: true
            },
            "subject": {
                required: true,
                minlength: 5
            },
            "message": {
                required: true,
                minlength: 25
            }
        },
        messages: {
            "name": {
                required: "Hm... Ns gostamos de saber o nome dos nossos contatos. Qual o seu?",
                minlength: "Teu nome  curtinho assim mesmo? Que tal preencher com teu sobrenome tambm?"
            },
            "email": {
                required: "Por favor, informe um e-mail para que possamos te responder...",
                email: "Este e-mail no parece ser vlido. Corrige a, vai!"
            },
            "subject": {
                required: "Que tal nos dizer o assunto da sua mensagem?",
                minlength: "Que tal um assunto mais claro? Este est muito curtinho ainda..."
            },
            "message": {
                required: "U... Escreve a sua mensagem! Ns prometemos ler e responder tudo. :)",
                minlength: " s isso mesmo? Quanto mais clara a sua mensagem for, mais clara ser nossa resposta! "
            }
        }
    });
});



